@import '_color';
@import '_function';

body {
    min-width: 1170px;
}

.full {
    width: 100%;
    height: 100%;
}

.page-1 {
    min-width: 1170px;
    margin: 0 auto;

    div.swiper1 {
        width: 100%;
        height: 800px;

        div.swiper-slide-active {
            a.lazy {
                img {
                    display: block;
                }
            }
        }

        a {
            display: block;
            width: 100%;
            height: 800px;
            position: relative;

            img {
                display: block;
                width: 1600px;
                height: 800px;
                position: absolute;
                left: 50%;
                top: 0;
                margin-left: -800px;
                display: none;
            }
        }
    }

    div.content-page {
        .page1 {
            background-color: #f7f7f7;
            overflow: hidden;

            img {
                background: none;
                display: block;
                width: 1078px;
                height: 463px;
                margin: 50px auto;
            }
        }

        .page2 {
            background-color: #252525;
            overflow: hidden;

            em.titles {
                color: #fff;

                i {
                    color: #8f8f8f;
                }
            }

            div.hovers {
                margin-top: 80px;
                width: 1235px;

                div.hovers-list {
                    overflow: hidden;
                    float: left;
                    width: 270px;
                    height: 300px;
                    overflow: hidden;
                    position: relative;
                    text-align: center;
                    margin-right: 30px;

                    &:hover {
                        span {
                            bottom: -50px;
                        }

                        div.tops {
                            top: 0;
                        }
                    }

                    span {
                        display: block;
                        background-color: rgba(0,0,0,0.4);
                        width: 100%;
                        height: 50px;
                        line-height: 50px;
                        color: #fff;
                        font-size: 18px;
                        position: absolute;
                        bottom: 0;
                        left: 0;
                        @include tran;
                    }

                    div.tops {
                        @include tran;
                        position: absolute;
                        left: 0;
                        top: -100%;
                        width: 100%;
                        height: 100%;
                        background-color: rgba(76,193,170,0.9);
                        font-size: 16px;
                        line-height: 2em;
                        color: #fff;

                        em {
                            margin-top: 45px;
                            margin-bottom: 10px;
                            display: inline-block;
                            border-bottom: 1px solid #fff;
                            padding-bottom: 12px;
                            font-size: 22px;
                        }
                    }
                }
            }

            div.peoples {
                width: 1250px;
                padding-bottom: 100px;
                margin-top: 70px;

                div.lists {
                    float: left;
                    margin-left: 5px;
                    margin-right: 56px;

                    .lazy-body {
                        border: 2px solid #474747;
                        width: 214px;
                        height: 214px;
                        padding: 13px;

                        .lazy {
                            display: block;
                            width: 214px;
                            height: 214px;
                        }
                    }

                    em {
                        text-align: center;

                        span {
                            color: #c59a6d;
                            margin: 15px 0;
                            display: block;
                        }

                        i {
                            display: block;
                            color: #777777;
                        }
                    }
                }
            }
        }

        .page3 {
            background-color: #f8f8f8;

            i.xin {
                &:before {
                    background-color: #f8f8f8;
                }
            }

            img {
                background: none;
                display: block;
                margin: 0 auto;
                width: 1098px;
                margin-top: 20px;
                height: 754px;
            }
        }

        .page4 {
            background-color: #fff;
            overflow: hidden;

            i.xin {
                &:before {
                    background-color: #fff;
                }
            }

            img.huo {
                width: 769px;
                height: 465px;
                margin: 60px auto;
                background: none;
            }

            div.swiper2 {
                margin-bottom: 30px;
                height: 260px;
                width: 100%;

                .pagination1 {
                    bottom: 0;
                }

                .swiper-pagination-switch {
                    background-color: transparent;
                    border: 1px solid #d1d1d1;
                }

                .swiper-active-switch {
                    background-color: #35bda3;
                    border-color: #35bda3;
                }
                width: 100%;

                div.swiper-slide {
                    height: 230px;
                }

                div.a-body {
                    width: 1200px;
                    overflow: hidden;
                }

                a {
                    float: left;
                    margin-right: 15px;
                    border: 1px solid #c7c7c7;
                    width: 180px;
                    height: 220px;
                    overflow: hidden;
                    text-align: center;

                    div.lazy {
                        width: 180px;
                        height: 112px;
                    }

                    span {
                        background-color: #35bda3;
                        color: #fff;
                        text-align: center;
                        font-size: 16px;
                        width: 120px;
                        display: block;
                        height: 30px;
                        line-height: 30px;
                        border-radius: 5px;
                        margin: 15px auto;
                    }

                    p {
                        width: 160px;
                        margin: 0 auto;
                        text-align: left;
                        @include line_over(2);
                        height: 42px;
                        color: #666;
                        font-size: 12px;
                    }
                }
            }
        }

        .page5 {
            background-color: #040404;
            overflow: hidden;

            em.titles {
                color: #fff;

                i {
                    color: #a9a9a9;
                }
            }

            div.qin {
                margin: 60px 0;
                overflow: hidden;
                height: 410px;
                position: relative;

                div.qin-list {
                    position: relative;
                    width: 70px;
                    @include tran($b:linear);
                    height: 100%;
                    background-color: #f46f46;
                    background-color: rgba(244,111,70,0.8);
                    float: left;
                    overflow: hidden;

                    &.even {
                        background-color: #4ab49f;
                        background-color: rgba(74,180,159,0.8);
                    }

                    &.on {
                        width: 820px;

                        div.qin-title {
                            opacity: 0;
                            top: 100%;
                        }

                        div.qin-left {
                            opacity: 1;
                        }
                    }
                }

                div.qin-left {
                    overflow: hidden;
                    text-align: center;
                    position: relative;
                    width: 760px;
                    height: 370px;
                    padding: 40px;
                    color: #fff;
                    opacity: 0;
                    overflow-y: auto;
                    @include tran($d:0.3s);

                    em {
                        display: block;
                        font-size: 30px;
                        margin-bottom: 25px;
                    }

                    span {
                        display: block;
                        font-size: 14px;
                    }

                    div {
                        margin-top: 30px;

                        i {
                            float: left;
                            text-align: left;
                            width: 150px;
                            height: 30px;
                            line-height: 30px;
                            font-size: 12px;
                            padding: 0 10px;
                            @extend .ellipsis;
                            background-color: rgba(0,0,0,0.08);
                            margin-right: 16px;
                            margin-bottom: 5px;
                        }
                    }
                }

                div.qin-title {
                    position: absolute;
                    width: 70px;
                    height: 100%;
                    color: #f8f8f8;
                    font-size: 20px;
                    overflow: hidden;
                    writing-mode: tb-rl;
                    text-align: center;
                    cursor: pointer;
                    right: 17px;
                    top: 0;
                    @include tran($d:0.3s);

                    i {
                        display: inline-block;
                        font-size: 24px;
                        writing-mode: lr-tb;
                    }
                }
            }
        }

        .page6 {
            padding-bottom: 100px;

            i.xin:before {
                background-color: #fff;
            }

            img {
                margin: 0 auto;
            }
        }

        .page7 {
            background-color: #2a2a2a;

            em.titles {
                span {
                    color: #fff;
                }

                i {
                    color: #aaaaaa;
                }
            }

            form {
                display: block;
                text-align: center;
                overflow: hidden;
                padding-bottom: 70px;

                div.input {
                    display: inline-block;
                    width: 370px;
                    height: 50px;
                    font-size: 14px;
                    margin: 65px 15px 0;
                    position: relative;

                    div.error {
                        position: absolute;
                        top: 100%;
                        left: 0;
                        line-height: 30px;
                        display: block;
                        margin-top: 15px;
                    }

                    input {
                        display: block;
                        width: 100%;
                        height: 100%;
                        text-indent: 1em;
                    }
                }

                button {
                    margin-top: 65px;
                    width: 370px;
                    font-size: 14px;
                    text-align: center;
                    line-height: 50px;
                    border: 1px solid #3cd8b9;
                    color: #3cd8b9;
                    background-color: transparent;
                    cursor: pointer;
                }
            }
        }
    }
}

body.page-2 {
    div.banner {
        background-color: #000;
        height: 800px;
        position: relative;

        img {
            position: absolute;
            left: 50%;
            margin-left: -800px;
            top: 0;
            display: block;
            height: 800px;
            background: none;
        }

        a {
            display: block;
            position: absolute;
            width: 110px;
            height: 30px;
            color: #fff;
            background-color: #1ea78d;
            text-align: center;
            line-height: 30px;
            font-size: 14px;
            left: 50%;
            margin-left: -55px;
            bottom: 180px;
            @include tran;

            &:hover {
                background-color: #fff;
                color: #1ea78d;
            }
        }
    }

    div.page1 {
        background-color: #f7f7f7;
        overflow: hidden;

        ul.swiper-btns {
            position: relative;
            height: 50px;
            border-bottom: 1px solid #f0f0f0;

            li {
                float: left;
                width: 20%;
                position: relative;
                height: 50px;
                line-height: 50px;
                text-align: center;
                color: #666;
                font-size: 14px;
                @include tran;
                @extend .no_select;
                cursor: pointer;

                &:hover {
                    @extend .on;
                }

                &.on {
                    color: #1ea78d;
                }

                &:after {
                    content: '';
                    position: absolute;
                    width: 1px;
                    display: block;
                    height: 14px;
                    background-color: #b7ddd6;
                    right: 0;
                    top: 50%;
                    margin-top: -7px;
                }
            }

            i {
                position: absolute;
                width: 20%;
                height: 2px;
                background-color: #1ea78d;
                bottom: -1px;
                left: 0;
                display: block;
                @include  tran;
            }

            &:after {
                content: '';
                position: absolute;
                width: 1px;
                display: block;
                height: 20px;
                background-color: #f7f7f7;
                right: 0;
                top: 50%;
                margin-top: -7px;
            }
        }

        div.swiper1 {
            width: 100%;
            height: 450px;
            overflow: hidden;

            a.lazy {
                display: block;
                width: 1200px;
                height: 450px;
                img{
                    display: none;
                }
            }
            .swiper-slide-active{
                a.lazy{
                    img{
                        display: block;
                    }
                }
            }
        }
    }
}

.pagination,
.pagination1 {
    position: absolute;
    z-index: 20;
    bottom: 10px;
    width: 100%;
    text-align: center;
}

.swiper-pagination-switch {
    display: inline-block;
    width: 8px;
    height: 8px;
    border-radius: 8px;
    background: #555;
    margin: 0 5px;
    opacity: 0.8;
    border: 1px solid #fff;
    cursor: pointer;
}

.swiper-active-switch {
    background: #fff;
}
